
@charset "UTF-8";
*, *::before, *::after {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  font-family: Lato, Helvetica, 微软雅黑, "Microsoft YaHei", sans-serif;
  font-size: 16px;
  color: #0F2446;
}
h6 {
  font-size: 1.0rem;
}
h5 {
  font-size: 1.25rem;
}
h4 {
  font-size: 1.5rem;
}
h3 {
  font-size: 1.75rem;
}
h2 {
  font-size: 2.0rem;
}
h1 {
  font-size: 2.25rem;
}
button {
  font-size: 16px;
}
button:hover {
    cursor: pointer;
}
a {
  color: #0F2446;
  text-decoration: none;
}
.wrapper {
  margin: 0 10px;
}
a {
  -webkit-transition: color 0.7s, background 0.7s, border 0.7s;
  transition: color 0.7s, background 0.7s, border 0.7s;
}
.flex {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.wrap {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.nowrap {
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.middle {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.space-between {
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.space-around {
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.center {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.block {
  display: block;
}
@media (min-width: 769px) {
.wrapper {
    margin: 0 50px;
}
}



